import React from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import ArrowCell from "./ArrowCell";
import canvas from '../common/Canvas'
import propsUtil from "./PropsUtil";

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const ConnectionEditor = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    
    if (!window.Module.inputReceiver().needConnectionEditing()) {
        props.reload();
        return null;
    }

    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    var editor = window.Module.startConnectionEditing();

    var rect = editor.getObjectBound();
    var pos = propsUtil.getPlacement(rect, propsUtil.connectionEditMaxHeight);

    const handleClickAway = (event) => {
        var paper = document.getElementById("connection_edit_paper");
        if (paper == null) {
            return;
        }
        var rect = paper.getBoundingClientRect();
        if (event.clientX >= rect.left && event.clientX <= rect.right &&
            event.clientY >= rect.top && event.clientY <= rect.bottom) {
            return;
        }
        window.Module.inputReceiver().setNeedConnectionEditing(false);
        props.reload();
    }

    const onChange = (side, event) => {
        event.preventDefault();
        if (side == 0) {
            editor.setStartArrow(event.target.value);
        } else {
            editor.setEndArrow(event.target.value);
        }
        reload();
        canvas.draw();
    }

    var arrowCount = editor.arrowCount();
    var start = editor.startArrowType();
    var end = editor.endArrowType();

    var arrowsLeft = []
    var arrowsRight = []
    for (var idx = 0; idx < arrowCount; idx++) {
        var i = idx;
        arrowsLeft.push(<MenuItem value={i}><ArrowCell side={1} type={i} width={50} height={15}></ArrowCell></MenuItem>);
        arrowsRight.push(<MenuItem value={i}><ArrowCell side={0} type={i} width={50} height={15}></ArrowCell></MenuItem>);
    }
    return (<ClickAwayListener onClickAway={handleClickAway}>
        <Popper open={true} {...pos}>
            <div id="connection_edit_paper" className="connection_edit_paper">
                <div className="connection_edit_header">
                    <span>Arrows</span>
                </div>
                <div className="connection_edit_options">
                    <Select
                        disableUnderline
                        className="connection_edit_options_cell"
                        id="demo-simple-select"
                        value={start}
                        onChange={(event)=>onChange(0, event)}
                    >
                        {arrowsLeft}
                    </Select>
                    <div className="connection_edit_options_cell_space"></div>
                    <Select
                        disableUnderline
                        className="connection_edit_options_cell"
                        id="demo-simple-select"
                        value={end}
                        onChange={(event)=>onChange(1, event)}
                    >
                        {arrowsRight}
                    </Select>
                </div>
            </div>
        </Popper>
    </ClickAwayListener>)
}

export default ConnectionEditor;