import React from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import propsUtil from "./PropsUtil";

const Help = (props) => {
    if (!window.Module.inputReceiver().needToShowShapeHelp()) {
        props.reload();
        return null;
    }

    var editor = window.Module.startPropertyEditing();
    if (!editor.isValid()) {
        window.Module.inputReceiver().setNeedToShowShapeHelp(false);
        props.reload();
        return null;
    }
    var helpText = editor.getHelpText();
    helpText = helpText.replace(" tap ", " click ");
    var rect = editor.getBound();

    var pos = propsUtil.getPlacement(rect, propsUtil.showHelpMaxHeight);

    const handleClickAway = () => {
        window.Module.inputReceiver().setNeedToShowShapeHelp(false);
        props.reload();
    }

    return (
        <ClickAwayListener onClickAway={handleClickAway}>
            <Popper open={true} {...pos}>
                <div className="shape_help_paper">
                    {<div dangerouslySetInnerHTML={{ __html: helpText }} />}
                </div>
            </Popper>
        </ClickAwayListener>
    );
}

export default Help;