import React, { useState } from 'react';
import { AppBar, Grid, Toolbar, IconButton, Menu, MenuItem } from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import Style from './HeaderStyle';
import { makeStyles } from "@material-ui/core/styles";
import NestedMenuItem from "material-ui-nested-menu-item";
import CheckIcon from '@material-ui/icons/Check';
import canvas from '../../common/Canvas'
import Dialog from '@material-ui/core/Dialog';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import DialogTitle from '@material-ui/core/DialogTitle';
import { BiCopy } from "react-icons/bi";
import { jsPDF } from "jspdf";
import Feedback from "./Feedback"
import { tip } from '../Tip/QuickTip'
import userInfo from '../../utils/UserInfo'
import ShareModal from "../DashboardItems/ShareModal";
import docInfo from '../../utils/DocInfo';
import ImageUploader from '../ImageUploader/ImageUploader';
import PageList from '../PageList/PageList';
import Config from '../../common/Config';
import PageSizeModal from '../PageSize/PageSize'
import uiConfig from '../../common/UIConfig';
import integ from '../../integ/integ';

var FileSaver = require('file-saver');

const useStyles = makeStyles(Style);

