const AppMenu = (props) => {
    const classes = useStyles();
    const [snap, setSnap] = useState(true);
    const [grid, setGrid] = useState(canvas.showGrid);

    const [value, setValue] = useState('');
    const [open, setOpen] = useState(false);

    const exportFile = (format) => {
        console.log("export ", format);
        setOpen(false)

        if (format == "pdf") {
            exportPdf();
        } else if (format == "png" || format == "jpeg") {
            var img = exportImage(format);
            if (img != null) {
                FileSaver.saveAs(img.data, "drawing." + format);
            }
        } else if (format == "svg") {
            var content = window.Module.getSvg();
            var blob = new Blob([content], {type: "text/plain;charset=utf-8"});
            FileSaver.saveAs(blob, "drawing.svg");
        } else if (format == "lekh") {
            var content = window.Module.getLekh();
            var blob = new Blob([content], {type: "text/plain;charset=utf-8"});
            FileSaver.saveAs(blob, "drawing.lekh");
        }
    }

    const subOptions = [
        { id: "pdf", name: 'PDF', handler: exportFile },
        { id: "png", name: 'PNG', handler: exportFile },
        { id: "jpeg", name: 'JPEG', handler: exportFile },
        { id: "svg", name: 'SVG', handler: exportFile },
        { id: "lekh", name: 'LEKH', handler: exportFile },
    ]

    var options = [
        /*{ id: 1, name: 'Insert diagram' }, */
        { id: "share", name: 'Share board', checkable: false },
        { id: "export", name: 'Export', submenu:  subOptions},
        { id: "insertImage", name: 'Insert Image', checkable: false},
        //{ id: "canvasSize", name: 'Canvas Size', checkable: false},
        { id: "grid", name: 'Show grid', checkable: true },
        { id: "snap", name: 'Snap objects', checkable: true },
        { id: "contact", name: 'Contact us', checkable: false },
        { id: "feedback", name: 'Feedback', checkable: false },
        { id: "tip", name: 'Show drawing tip', checkable: false },
        { id: "help", name: 'Help', checkable: false},
    ];
    if (window.matchMedia(Config.mediaQuery).matches) {
        options.splice(2, 0, { id: "managePages", name: 'Manage Pages', checkable: false})
    }

    if (uiConfig.isIntegMode()) {
        options = integ.customizeAppMenu(options);
    }

    const handleClick = (e) => {
        setValue(e.currentTarget)
        setOpen(true)
    }

    const getChecked = function(id) {
        if (id == 'grid') {
            return grid;
        } else if (id == 'snap') {
            return snap;
        }
        return false;
    }

    const shareLoggedInBoard = () => {
        var dialog = <ShareModal state={true} close={() => props.showDialog(null)} doc={docInfo.data} />;
        props.showDialog(dialog);
    }
    const shareBoard = function() {
        if (userInfo.isLoggedIn() && docInfo.data && docInfo.data.docid) {
            shareLoggedInBoard();
            return;
        }
        const handleClose = function() {
            props.showDialog(null);
        };

        const copyLink = ()=>{
            document.getElementById("share-link").select(); 
            document.execCommand("copy");
        };

        const SearchButton = () => (
            <IconButton onClick={copyLink}>
              <BiCopy />
            </IconButton>
            )

        var url = window.location;
        var dialog = (
        <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={true}>
        <DialogTitle id="simple-dialog-title">Get the shareable link from below</DialogTitle>
        <TextField id="share-link" variant="outlined" size="small" defaultValue={url} InputProps={{
              readOnly: true,
              endAdornment: <SearchButton/>
            }}/>
        <Button onClick={handleClose}>OK</Button>
      </Dialog>
      )
      props.showDialog(dialog)
    }
    const giveFeedback = function () {
      const handleClose = function () {
        props.showDialog(null)
      }

      const feedback = (
        <Dialog onClose={handleClose} aria-labelledby="feedback" open={true}>
          <Feedback handleClose={handleClose} />
        </Dialog>
      )

      props.showDialog(feedback)
    }

    const handleMenuClick = (id) => {
        if (id == 'insertImage') {
            var at = canvas.getMidPoint();
            at = canvas.mapPtUIScroll(at);
            at = {x: canvas.maprx(at.x), y: canvas.mapry(at.y)};
            props.showDialog(<ImageUploader dialogHandler={props.showDialog} at={at}/>);
        }

        if (id == 'managePages') {
            props.showDialog(<PageList close={()=>{props.showDialog(null)}}/>);
        }

        if (id == 'canvasSize') {
            props.showDialog(<PageSizeModal close={()=>{props.showDialog(null)}}/>);
        }

        if (id == "grid") {
            setGrid(!grid);
            canvas.showGrid = !grid;
            canvas.drawGrid();
        }
        if (id == "snap") {
            setSnap(!snap);
            window.Module.setSnap(!snap);
        }
        if (id == "help") {
            window.open("https://lekh.app/help.html", '_blank').focus();
        }
        
        if (id == "contact") {
            window.open("https://lekh.app/contact.html", '_blank').focus();
        }

        if (id == "share") {
            shareBoard();
        }
        if (id == "feedback") {
          giveFeedback()
        }

        if (id == "tip") {
            tip.show();
        }

        setOpen(false)
    }

    const exportImage = (format) => {
        var img = null;
        try {
            img = canvas.exportToImage(format);
        } catch (e) { }

        if (img == null || img.data == "data:,") {
            const handleClose = () => props.showDialog(null);
            var dialog = (
                <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={true}>
                    <DialogTitle id="simple-dialog-title">Unable to export. Canvas is probably too large. Please contact support info@lekhapp.com</DialogTitle>
                    <Button onClick={handleClose}>OK</Button>
                </Dialog>
            )
            props.showDialog(dialog)
            return null;
        }

        return img;
    }

    const exportPdf  = () => {
        var img = exportImage("jpeg");
        if (img == null) {
            return;
        }
        var orient = "landscape";
        if (img.height > img.width) {
            orient = "portrait";
        }
        var doc = new jsPDF({
            orientation: orient,
            format: [img.width, img.height]
        })
        doc.addImage(img.data, 'JPEG', 0, 0, img.width, img.height)
        doc.save("drawing.pdf");

        // The following code uses canvas2pdf which is not working
        // Two ways to fix:
        // 1) Fix the canvas2Pdf and use pdfkit
        // 2) Use jsPdf which also provide html canvas like API
        //
        // In either case we need to change the JSPlatform code to 
        // not use Path2D class as it is marked as experimental
        //
        ////Create a new PDF canvas context.
        //var ctx = new window.canvas2pdf.PdfContext(window.blobStream());
        //
        ////draw your canvas like you would normally
        ////ctx.fillStyle='yellow';
        ////ctx.fillRect(100,100,100,100);
        //// more canvas drawing, etc...
        //window.Module.drawCanvasForExport(ctx, 0, 0);

        ////convert your PDF to a Blob and save to file
        //ctx.stream.on('finish', function () {
        //    var blob = ctx.stream.toBlob('application/pdf');
        //    FileSaver.saveAs(blob, "drawing.pdf");
        //});
        //ctx.end();
    }

    const handleLogoClick = (e) => {
        if (uiConfig.isIntegMode()) {
            integ.handleLogoClick(e);
        } else {
            window.location.href="/";
        }
    }

    return (
        <Grid container>
            <AppBar className="HeaderSection" position='static' color='transparent' style={{ background: 'transparent', boxShadow: 'none' }}>
                <Toolbar>
                    <IconButton className="HeaderMenuIcon" color="inherit" onClick={handleLogoClick}>
                        <img
                            src={require("../../assests/icons/lekhboard-small.svg").default}
                            className="w-24"
                            alt="Logo"
                        />
                    </IconButton>
                    <div style={{width: 10, height: 30}}></div>
                    <IconButton className="HeaderMenuIcon" color="inherit" aria-label="menu" onClick={(e) => handleClick(e)}>
                        <MenuIcon />
                    </IconButton>
                    <Menu
                        onClose={() => setOpen(false)}
                        open={open}
                        classes={{
                            paper: classes.appMenu
                        }}
                        value={value}
                        PaperProps={{
                            style: {
                                width: 200,
                                marginTop: 40
                            },
                        }}>
                        {options?.map((option, index) => {
                            var checked = null;
                            if (option.checkable && getChecked(option.id)) {
                                checked = <span className={classes.checkIcon}><CheckIcon /></span>;
                            }
                            if (option.submenu) {
                                return (<NestedMenuItem
                                    style={{ left: '15px' }}
                                    label={option.name}
                                    parentMenuOpen={open}
                                    className="subIcon"
                                    key={option.id}>
                                    {
                                    option.submenu.map((subOption, index) => (
                                        <MenuItem key={index} onClick={()=>{setOpen(false); subOption.handler(subOption.id)}}>{subOption.name}</MenuItem>
                                    ))}
                                </NestedMenuItem>);
                            }
                            const handleClick = () => {
                                if (!!option.handler) {
                                    setOpen(false);
                                    option.handler(option.id);
                                } else {
                                    handleMenuClick(option.id);
                                }
                            }
                            return (
                                <div key={option.id}>
                                    { checked }
                                    <MenuItem style={{ left: '15px' }} value={option.name} 
                                        onClick={handleClick} key={option.id}>
                                        {option.name}
                                    </MenuItem>
                                </div>
                            )
                        })}
                    </Menu>
                </Toolbar>
            </AppBar>
        </Grid>
    )
}

export default AppMenu;
