import './Tip.css'

import React from 'react'
import { Dialog } from "@material-ui/core";
import Button from '@material-ui/core/Button';
import { AiOutlineClose } from "react-icons/ai"
import uiConfig from '../../common/UIConfig';

export const tip = {
    force: false,
    closed: false,
    reload: () => {},
    showDoNotShowButton: true,
    show: () => {tip.force = true; tip.closed=false; tip.showDoNotShowButton = false; tip.reload();}
}

const getStorage = () => {
    if (typeof(window.Storage) == "undefined") {
        return null;
    }
    try {
        return window.localStorage;
    } catch(e) { }

    return null;
}

tip.showing = () => {
    if (tip.closed) {
        return false
    }

    if (!uiConfig.shouldShowQuickTip()) {
        return false;
    }

    if (tip.force) {
        return true;
    }

    var storage = getStorage();
    if (!storage) {
        return false;
    }
    var showTip = storage.getItem("showTip");
    if (showTip == null || typeof(showTip) == 'undefined') {
        return true;
    }

    return showTip != "true";
}

tip.doNotShowAgain = () => {
    var storage = getStorage();
    if (!storage) {
        return;
    }
    storage.setItem("showTip", "true");
}

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
      () => { stateRef.current = state },
      [state]
    )
    return [state, stateRef, setState];
}

const QuickTip = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = () => {
        setCounter(counterRef.current + 1);
    }
    React.useEffect(() => {
        tip.reload = () => {
            reload();
        }

        window.setTimeout(()=> {
            var vid = document.getElementById("tip_video");
            if (vid != null && vid.paused) {
                vid.setAttribute("controls", "controls")
            }
        }, 200);
    }, [])

    if (!tip.showing()) {
        return null;
    }

    const handleClose = () => {
        tip.closed = true;
        tip.force = false;
        reload();
    }

    const doNotShowAgain = () => {
        handleClose();
        tip.doNotShowAgain();
    }

    return <Dialog onClose={handleClose} open={true}>
        <div className="tip_container">
            <div className="tip_header">
                <span className="tip_title">Drawing Tip</span>
                <AiOutlineClose className="tip_close" onClick={() => handleClose()}></AiOutlineClose>
            </div>
            <div>
                <video id="tip_video" className="tip_video" autoPlay muted loop>
                    <source src="a1/videos/QuickTip.mp4" type="video/mp4" />
                </video>
            </div>
            {tip.showDoNotShowButton && <div className="tip_donot_show" >
                <Button onClick={doNotShowAgain}>Do not show again</Button>
            </div>}
        </div>
    </Dialog>
}

export default QuickTip;