import React from "react"
import { AiOutlinePlus, AiOutlineMinus, AiOutlineAlignCenter, 
    AiOutlineAlignLeft, AiOutlineAlignRight, AiOutlineVerticalAlignBottom,
    AiOutlineVerticalAlignMiddle, AiOutlineVerticalAlignTop } from "react-icons/ai";
import IconButton from '@material-ui/core/IconButton';
import ColorList from '../common/ColorList';
import canvas from '../common/Canvas'

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const FontEditor = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = () => {
        setCounter(counterRef.current + 1);
    }
    let curColor = props.editor.style().effectiveTextFill().solidColor;
    let fontSz = props.editor.style().effectiveFontSize();
    var alignV = props.editor.style().effectiveTextAlignVertical().value;
    var alignHz = props.editor.style().effectiveTextAlignHorizontal().value;

    const onColorChange = (color) => {
        props.editor.setTextColor(color);
        canvas.draw();
        reload();
    }

    const onFontPlus = () => {
        props.editor.setFontSize(fontSz + 0.5);
        canvas.draw();
        reload();
    }

    const onFontMinus = () => {
        const val = fontSz - 0.5;
        if (val <= 0) {
            return;
        }
        props.editor.setFontSize(val);
        canvas.draw();
        reload();
    }

    const changeHzAlign = (val) => {
        props.editor.setHorizontalAlignment({value:val});
        canvas.draw();
        reload();
    }

    const changeVAlign = (val) => {
        props.editor.setVerticalAlignment({value:val});
        canvas.draw();
        reload();
    }

    return (<div className="shape_style_line">
        <div className="shape_style_line_width">
            <IconButton aria-label="plus" onClick={onFontMinus}>
                <AiOutlineMinus />
            </IconButton>
            <div className="shape_style_line_width_text">{fontSz.toFixed(2)}</div>
            <IconButton aria-label="minus" onClick={onFontPlus}>
            <AiOutlinePlus />
            </IconButton>
        </div>
        <div className="shape_style_font_align" style={{paddingTop:5, borderTop: "1px solid #bebebe"}}>
            <div className={alignHz == 2 ? "shape_style_font_align_left changeBorder": "shape_style_font_align_left"}>
                <AiOutlineAlignLeft onClick={()=>changeHzAlign(2)}/>
            </div>
            <div className={alignHz == 1 ? "shape_style_font_align_center changeBorder": "shape_style_font_align_center"}>
                <AiOutlineAlignCenter onClick={()=>changeHzAlign(1)}/>
            </div>
            <div className={alignHz == 3 ? "shape_style_font_align_right changeBorder": "shape_style_font_align_right"}>
                <AiOutlineAlignRight onClick={()=>changeHzAlign(3)}/>
            </div>
        </div>
        <div className="shape_style_font_align" style={{paddingTop:5, marginTop: 6, borderBottom: "1px solid #bebebe"}}>
            <div className={alignV == 2 ? "shape_style_font_align_left changeBorder": "shape_style_font_align_left"}>
                <AiOutlineVerticalAlignTop onClick={()=>changeVAlign(2)}/>
            </div>
            <div className={alignV == 1 ? "shape_style_font_align_center changeBorder": "shape_style_font_align_center"}>
                <AiOutlineVerticalAlignMiddle onClick={()=>changeVAlign(1)}/>
            </div>
            <div className={alignV == 3 ? "shape_style_font_align_right changeBorder": "shape_style_font_align_right"}>
                <AiOutlineVerticalAlignBottom onClick={()=>changeVAlign(3)}/>
            </div>
        </div>
        <div className="shape_style_line_colors">
            <ColorList curColor={curColor} type='text' onColorChange={onColorChange} />
        </div>
    </div>)
}

export default FontEditor;