import React from 'react'
import { FaCheckCircle } from "react-icons/fa";
import {FaCheck} from "react-icons/fa";
import { RiCheckboxBlankCircleFill } from "react-icons/ri";

const ChessCircle = (props) => {
    if (props.checked) {
        return <div id={props.id} className="chess">
            <FaCheck strokeWidth={4}/>
        </div>
    }
    return <div id={props.id} className="chess"></div>
}

const WhiteCircle = (props) => {
    var style = {background: "#ffffff", border: "1px solid #101010"}
    if (props.checked) {
        return <div id={props.id} className="chess" style={style}>
            <FaCheck strokeWidth={4}/>
        </div>
    }
    return <div id={props.id} className="chess" style={style}></div>
}

