var fillColors = [
    <ChessCircle id={0}/>
]

fillColors = fillColors.concat(lineColors);
fillColors = fillColors.concat(extraColors);

const getSelectedIndex = (colors, curColor, type) => {
    if (curColor.alpha == 0 && type == 'fill') {
        return 0;
    }
    let idx = 0;
    let dist = 9999999999; //some big number
    colors.map((component, index) => {
        if (type == 'fill' && index == 0) {
            return;
        }
        var color = ColorNameToVal(component.props.color);
        let dr = color[0] - curColor.red;
        let dg = color[1] - curColor.green;
        let db = color[2] - curColor.blue;
        var tmp = Math.sqrt(dr * dr + dg * dg + db * db);
        if (tmp < dist) {
            dist = tmp;
            idx = index;
        }
    });
    return idx;
}

const ColorList = (props) => {
    let colors = [];
    if (props.type == 'fill') {
        colors = fillColors;
    } else {
        colors = lineColors;
    }

    var selectedIndex = getSelectedIndex(colors, props.curColor, props.type);

    const onColorChange = (index, color) => {
        var val = {red: 0, green:0, blue:0, alpha: 0};
        if (props.type == 'fill' && index == 0) {
            val = {red: 0, green:0, blue:0, alpha: 0};
        } else {
            var cl = ColorNameToVal(color);
            val = {red: cl[0], green: cl[1], blue: cl[2], alpha: cl[3]};
        }
        props.onColorChange(val);
    }

    return colors?.map((color, index)=>{
        var item = null;
        if (index == selectedIndex) {
            if (props.type == 'fill' && index == 0) {
                item = <ChessCircle checked={true}/>
            } else if (color.props.color == 'white') {
                item = <WhiteCircle checked={true} size="2em" color={color.props.color}/>;
            } else {
                item = <FaCheckCircle size="2em" color={color.props.color}/>;
            }
        } else {
            item = color;
        }
        return (
            <span key={color.props.id} onClick={() => onColorChange(index, color.props.color)}>
              {item}
            </span>
        )
    })
}
export const ColorListOld = (props) => {
    const {colors} = props;
    return colors?.map((color, index)=>{
        var item = null;
        if (index == props.selectedIndex) {
            item = <FaCheckCircle size="2em" color={color.props.color}/>;
        } else {
            item = color;
        }
        return (
            <span onClick={() => props.onColorChange(index, color.props.color)}>
              {item}
            </span>
        )
    })
}

export default ColorList;
