import React, { useRef, useEffect } from 'react'

const ArrowCell = props => {
  const canvasRef = useRef(null)
  var dpr = window.devicePixelRatio || 1;
  var width = props.width;
  var height = props.height;

  const drawCanvas = (canvasElement) => {
    canvasElement.width = width * dpr;
    canvasElement.height = height * dpr;
    canvasElement.style.width = width + "px";
    canvasElement.style.height = height + "px";
    const context = canvasElement.getContext('2d')  
    context.scale(dpr, dpr);
    context.fillStyle = "#ffffff00";
    context.fillRect(0, 0, canvasElement.width, canvasElement.height);
    window.Module.drawArrowCell(props.side, props.type, width, height, context);
  }

  if (canvasRef != null && canvasRef.current != null) {
    drawCanvas(canvasRef.current)
  }
  
  useEffect(() => {
    const canvasElement = canvasRef.current;
    drawCanvas(canvasElement);
  }, [])
  
  return <canvas ref={canvasRef} {...props} width={width+"px"} height={height+"px"}/>
}

export default ArrowCell