import React from "react"
import Popper from '@material-ui/core/Popper';
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import LineStyle from "./LineStyle";
import FillColor from "./FillColor";
import FontEditor from "./FontEditor"
import propsUtil from "./PropsUtil";

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const StyleEditor = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const [selectedTab, setSelectedTab] = React.useState(0);
    if (!window.Module.inputReceiver().needStyleEditing()) {
        props.reload();
        return null;
    }

    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    var editor = window.Module.startStyleEditing();

    var rect = editor.getObjectBound();
    var pos = propsUtil.getPlacement(rect, propsUtil.styleEditMaxHeight);

    var tabs = ["LINE", "FILL", "TEXT"];
    const handleClickAway = () => {
        window.Module.inputReceiver().setNeedStyleEditing(false);
        props.reload();
    }

    return <ClickAwayListener onClickAway={handleClickAway}>
        <Popper open={true} {...pos}>
            <div className="shape_style_paper">
                <div className="shape_style_tabs">
                    {tabs.map((tab, index)=> {
                        if (selectedTab == index) {
                            return <div key={index} className="shape_style_tab shape_style_tab_active"><span onClick={()=>setSelectedTab(index)} className="shape_style_tab_span_active">{tab}</span></div>
                        } else {
                            return <div key={index} className="shape_style_tab"><span className="shape_style_tab_span" onClick={()=>setSelectedTab(index)}>{tab}</span></div>        
                        }
                    })}
                </div>
                {selectedTab == 0 && <LineStyle editor={editor}/>}
                {selectedTab == 1 && <FillColor editor={editor}/>}
                {selectedTab == 2 && <FontEditor editor={editor}/>}
            </div>
        </Popper>
    </ClickAwayListener>
}

export default StyleEditor;