import React from "react"
import ColorList from '../common/ColorList';
import canvas from '../common/Canvas'

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
        () => { stateRef.current = state },
        [state]
    )
    return [state, stateRef, setState];
}

const FillColor = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = () => {
        setCounter(counterRef.current + 1);
    }

    let curColor = {red:0, green:0, blue:0, alpha: 0};
    if (props.editor.style().effectiveFillPaint().type.value == 2) {
        curColor = props.editor.style().effectiveFillPaint().solidColor;
    }
    const onColorChange = (color) => {
        props.editor.setFillColor(color);
        canvas.draw();
        reload();
    }

    return (<div className="shape_style_line_colors" style={{marginTop: 10}}>
        <ColorList curColor={curColor} type='fill'
            onColorChange={onColorChange} />
    </div>);
}

export default FillColor;