import { AiOutlineClose } from "react-icons/ai";
import { Dialog, TextField } from "@material-ui/core";
import Select from "react-select";
import React, { useEffect } from "react";

const options = [
    { label: 'Infinite', value: [0, 0] },
    { label: 'A3 (11.7 x 16.5)', value: [11.7, 16.5] },
    { label: 'A4 (8.3 x 11.7)', value: [8.3, 11.7] },
    { label: 'A5 (5.8 x 8.3)', value: [5.8, 8.3] },
    { label: 'Letter (8.5 x 11)', value: [8.5, 11] },
    { label: 'Legal (8.5 x 14)', value: [8.5, 14] },
    { label: 'Executive (7.2 x 10.5)', value: [7.2, 10.5] },
    { label: 'Tabloid (11 x 17)', value: [11, 17] },
    { label: 'Folio (8.5 x 13)', value: [8.5, 13] },
    { label: 'Statement (5.5 x 8.5)', value: [5.5, 8.5] },
    { label: 'Custom', value: [0, 0] },
]

var pageEditor = null;

const equal = (val1, val2) => {
    return Math.abs(val1 - val2) < 0.001;
}

const PageSizeModal = (props) => {
    const [selectedOption, setSelectedOption] = React.useState(options[0]);
    
    const [customWidth, setCustomWidth] = React.useState(0);
    const [customHeight, setCustomHeight] = React.useState(0);

    const [marginWidth, setMarginWidth] = React.useState(0);
    const [marginHeight, setMarginHeight] = React.useState(0);

    if (pageEditor == null) {
        pageEditor = window.Module.startPageEditing();
    }

    const handleChange = (newOption) => {
        var sz = pageEditor.getSize();
        if  (newOption.label == "Custom") {
            if (sz.width <= 0 || sz.height <= 0) {
                sz.width = 8.5;
                sz.height = 11;
            }
            var wd = parseFloat(sz.width).toFixed(2);
            setCustomWidth(wd);

            var ht = parseFloat(sz.height).toFixed(2);
            setCustomHeight(ht);

            setCustomSize(wd, ht);
        } else if (newOption.label == 'Infinite') {
            pageEditor.setSize(window.Module.CanvasSizeType.Infinite, {width: newOption.value[0], height: newOption.value[1], unit: window.Module.Unit.Inch}, newOption.label)
        } else {
            pageEditor.setSize(window.Module.CanvasSizeType.Fixed, {width: newOption.value[0], height: newOption.value[1], unit: window.Module.Unit.Inch}, newOption.label)
        }
        setSelectedOption(newOption);
    }

    const setCustomSize = (width, height) => {
        try {
            var wd = parseFloat(width);
            var ht = parseFloat(height);
        } catch (e) {
            return;
        }
        wd = wd <= 0? 0.5 : wd;
        ht = ht <= 0? 0.5: ht;

        pageEditor.setSize(window.Module.CanvasSizeType.Fixed, {width: wd, height: ht, unit: window.Module.Unit.Inch}, "Custom")
    }

    const setMarginSize = (width, height) => {
        try {
            width = parseFloat(width);
            height = parseFloat(height);
        } catch (e) {
            width = 0;
            height = 0;
        }
        
        pageEditor.setMarginSize({width: width, height: height, unit: window.Module.Unit.Inch})
    }

    const onCustomWidth = (e) => {
        var val = e.currentTarget.value;
        setCustomWidth(val);
        setCustomSize(val, customHeight);
    }

    const onCustomHeight = (e) => {
        var val = e.currentTarget.value;
        setCustomHeight(val);
        setCustomSize(customWidth, val);
    }

    const onMarginWidth = (e) => {
        var val = e.currentTarget.value;
        setMarginWidth(val);
        setMarginSize(val, marginHeight);
    }

    const onMarginHeight = (e) => {
        var val = e.currentTarget.value;
        setMarginHeight(val);
        setMarginSize(marginWidth, val);  
    }

    const isInfinite = selectedOption.label == 'Infinite';
    const isCustom = selectedOption.label == 'Custom';

    useEffect(() => {
        pageEditor.start();
        var cst = pageEditor.getCanvasType();
        var sz = pageEditor.getSize();
        if (cst.value == 0 || cst.value == 2) {
            setSelectedOption(options[0]);
        } else {
            var found = false;
            for (let item of options) {
                if (equal(item.value[0], sz.width) && equal(item.value[1], sz.height)) {
                    setSelectedOption(item);
                    found = true;
                    break;
                }
            }
            if (!found) {
                setCustomWidth(sz.width);
                setCustomHeight(sz.height);
                setSelectedOption(options[options.length - 1]);
            }
        }

        var margin = pageEditor.getMarginSize();
        if (margin.show) {
            setMarginWidth(margin.size.width);
            setMarginHeight(margin.size.height);
        } else {
            setMarginWidth(0);
            setMarginHeight(0);
        }
    }, []);

    const selectStyles = {
        menu: base => ({
          ...base,
          zIndex: 100
        })
      };
  
    return (
        <Dialog open={true} className="px-5" aria-labelledby="PageSize">
            <div className={`py-4 w-64 lg:w-96 bg-white h-80`}>
                <div className="px-3 flex justify-between items-center mb-3">
                    <div />
                    <h4 className="text-center m-0 text-lg">Set Canvas Size</h4>
                    <AiOutlineClose
                        className="text-xl cursor-pointer"
                        onClick={() => {props.close();}}
                    />
                </div>
                <hr />
                <div className="px-5 mt-1">
                    <Select
                        value = {selectedOption}
                        options={options}
                        className="mb-3"
                        onChange={handleChange}
                        placeholder=""
                        defaultValue=""
                        maxMenuHeight={180}
                        styles={selectStyles}
                    />
                    {isCustom &&
                    <>
                        <div className="h-4"></div>
                        <p className="m-0 mb-1 font-medium">Custom Size (Inch)</p>
                        <div className="flex">
                            <TextField id="custom-width" label="Width" variant="outlined" value={customWidth} onChange={onCustomWidth}/>
                            <div className="w-8"></div>
                            <TextField id="custom-height" label="Height" variant="outlined" value={customHeight} onChange={onCustomHeight}/>
                        </div>
                    </>
                    }
                    {!isInfinite &&
                    <>
                        <div className="h-4"></div>
                        <p className="m-0 mb-1 font-medium">Page Margin (Inch)</p>
                        <div className="flex">
                        <TextField id="margin-width" label="Width" variant="outlined" value={marginWidth} onChange={onMarginWidth}/>
                        <div className="w-8"></div>
                        <TextField id="margin-height" label="Height" variant="outlined" value={marginHeight} onChange={onMarginHeight}/>
                        </div>
                    </>
                    }
                </div>
            </div>
        </Dialog>
    );
};

export default PageSizeModal;
