import canvas from "../common/Canvas"

var propsUtil = { 
    connectionEditMaxHeight: 100,
    showHelpMaxHeight: 150,
    propertyEditMaxHeight: 200,
    styleEditMaxHeight: 300
}

const getTopPlacement = (rect) => {
    const getBoundingClientRect = () =>  {
        return {
            x: rect.x,
            y: rect.y,
            width:rect.width,
            height: rect.height,
            top: rect.y,
            left: rect.x,
            bottom: rect.y + rect.height,
            right: rect.x + rect.width
        }
    }

    var anchorEl = {
        clientWidth: rect.width,
        clientHeight: rect.height,
        getBoundingClientRect,
    }

    return {anchorEl: anchorEl, placement: 'top'}
}

const getCenterPlacenemt = (rect) => {
    return {style: { position: 'fixed', top: '50%', left: '50%', transform: 'translate(-50%, -50%)' }}
}

propsUtil.getPlacement = (rect, maxHeight) => {
    var vpp = canvas.toViewPort({x: rect.x, y: rect.y});
    rect.x = vpp.x;
    rect.y = vpp.y;
    
    rect.y -= 8;
    rect.height += 16;

    var tp = document.getElementById("transparentPanel");
    if (tp == null) {
        return getCenterPlacenemt(rect);
    }
    var tpRect = tp.getBoundingClientRect();
    if (rect.y < maxHeight && tpRect.height - (rect.y + rect.width) < maxHeight) {
        return getCenterPlacenemt(rect);

    }
    return getTopPlacement(rect);
}

export default propsUtil;