import React from 'react'
import { AiOutlinePlus, AiOutlineMinus, AiOutlineDash, AiOutlineSmallDash } from "react-icons/ai";
import IconButton from '@material-ui/core/IconButton';
import ColorList from '../common/ColorList';
import canvas from '../common/Canvas'

const dashLines = [
    {icon: <AiOutlineMinus size="2.2em" id="0" />},
    {icon:<AiOutlineSmallDash size="2em" style={{marginBottom:'3px'}}  id="1"  />},
    {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}   id="2" />},
    {icon: <AiOutlineDash size="2em" style={{marginBottom:'3px'}}  id="3" />},
]

const useRefState = initialValue => {
    const [state, setState] = React.useState(initialValue)
    const stateRef = React.useRef(state)
    React.useEffect(
      () => { stateRef.current = state },
      [state]
    )
    return [state, stateRef, setState];
}

const LineStyle = (props) => {
    const [counter, counterRef, setCounter] = useRefState(0);
    const reload = () => {
        setCounter(counterRef.current + 1);
    }
    let strokeWidth = props.editor.style().effectiveStrokeWidth();
    let curColor = props.editor.style().effectiveStrokePaint().solidColor;
    let dashType = props.editor.getDashType();

    const onDashChange = (val) => {
        props.editor.setDashType(val);
        canvas.draw();
        reload();
    }

    const onColorChange = (color) => {
        props.editor.setLineColor(color);
        canvas.draw();
        reload();
    }

    const onStrokeWidthPlus = () => {
        props.editor.setStrokeWidth(strokeWidth + 0.5);
        canvas.draw();
        reload();
    }

    const onStrokeWidthMinus = () => {
        const val = strokeWidth - 0.5;
        if (val <= 0) {
            return;
        }
        props.editor.setStrokeWidth(val);
        canvas.draw();
        reload();
    }

    return (<div className="shape_style_line">
        <div className="shape_style_line_width">
            <IconButton aria-label="minus" onClick={onStrokeWidthMinus}>
                <AiOutlineMinus />
            </IconButton>
            <div className="shape_style_line_width_text">{strokeWidth.toFixed(2)}</div>
            <IconButton aria-label="plus" onClick={onStrokeWidthPlus}>
                <AiOutlinePlus />
            </IconButton>
        </div>
        <div className="shape_style_line_dash">
            {dashLines.map((dashLine, index) => {
                var cls = "shape_style_line_dash_cell";
                if (index == dashType) {
                    cls = "shape_style_line_dash_cell changeBorder"
                }
                return (
                    <div key={index} className={cls} onClick={()=>onDashChange(index)}>
                        {dashLine.icon}
                    </div>
                )
            })}
        </div>
        <div className="shape_style_line_colors">
            <ColorList curColor={curColor} type='line' onColorChange={onColorChange} />
        </div>
    </div>)
}

export default LineStyle;
