import './App.css';
import AppMenu from './components/Header/Menu';
import Footer from './components/Footer/Footer';
import SideBarMenu from './components/SideBarMenu/SideBarMenu';
import Content from './components/SideBarMenu/Content';
import Layover from './components/Layover/Layover';
import React from "react";
import initializer from './common/AppInit'
import ContextMenu from './common/ContextMenu'
import AppInput from './common/AppInput'
import menuState from './common/MenuState'
import keyboardInput from './common/KeyboardInput'
import {DragPanel} from './draggable/Panel'
import {ShapeProps} from './shapeprops/ShapeProps';
import QuickTip from './components/Tip/QuickTip';
import userInfo from './utils/UserInfo';
import docInfo from './utils/DocInfo'
import {claimDoc} from './utils/ApiRequests'
import GesturePanel from './gestures/GesturePanel';
import uiConfig from './common/UIConfig';
import integ from './integ/integ';
import { Button } from '@material-ui/core';

class AppComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      wsState: "init",
      dialog: null, 
      ctxMenu: false,
      ctxMenuPos:{x:0, y:0}, 
      sideMenuSelectedItem: menuState.selected,
      isLoggedIn: userInfo.isLoggedIn(),
      docInfoState: docInfo.state,
      cameraActive: false
    };
    this.wsCallback = this.handleWSState.bind(this);
    this.showDialog = this.showDialog.bind(this);
    this.showContextMenu = this.showContextMenu.bind(this);

    menuState.event.addListener((e) => {
      if (this.state.cameraActive != menuState.cameraActive) {
        this.setState({cameraActive: menuState.cameraActive})
      }
    });

    userInfo.event.addListener(() => {
      if (this.state.isLoggedIn && !userInfo.isLoggedIn()) {
        // user logged out, reload the page
        window.location.reload();
        return;
      }
      if (this.state.isLoggedIn && userInfo.isLoggedIn()) {
        return;
      }

      this.setState({isLoggedIn: userInfo.isLoggedIn()});
      docInfo.get();
    });

    docInfo.onChange = () => {
      this.setState({docInfoState: docInfo.state});
      initializer.onDocDetailFetch();
    }
    userInfo.get();
  }

  handleWSState(state) {
    this.setState({wsState: state});
  }

  showDialog(dialog) {
    this.setState({dialog:dialog});
  }

  showContextMenu(show, x, y) {
    if (show) {
      this.setState({ctxMenu: true, ctxMenuPos:{x:x, y:y}});  
    } else {
      this.setState({ctxMenu: false});
    }
  }

  publishChangesForIntegMode() {
    integ.publishEditorChanges().catch(e => {

    });
  }

  render() {
    const loading =
      (this.state.wsState != "ready" && this.state.wsState != "offline") || 
      userInfo.state == 'loading' ||
      (userInfo.isLoggedIn() && docInfo.state == 'loading');
  
    var layover = null;
    if (loading) {
      layover = <div className="layover"><Layover wsstate={this.state.wsState}/></div>;
    }
    keyboardInput.showingDialog = this.state.dialog != null || layover != null || this.state.ctxMenu;
    var readOnly = window.wsmanager && window.wsmanager.readOnly();
    var docName = docInfo.data.name || '';
    if (docName.length > 34) {
      docName = docName.substr(0, 32) + '..';
    }
    const moveDoc = () => {
      claimDoc(docInfo.docid).then(()=>{
        window.location.reload();
      }).catch((err) =>{
      })
    }

    let loginLink = <a target="_blank" href="/#/login" className="help-link">Login</a>;
    let moveLink = <a href="javascript:void(0)" onClick={moveDoc} className="help-link">Move</a>;
    
    return (
      <div className="container">
        <Content />
        <div className="top-right">
          {!loading && !userInfo.isLoggedIn() && <a target="_blank" href="/#/login" className="help-link top-right-login">Login</a>}
          {uiConfig.shouldShowPublishCancelButton() && <Button variant="contained" style={{ fontWeight: 'bold', backgroundColor: '#0052cc', color: 'white', marginRight: '14px' }} onClick={this.publishChangesForIntegMode.bind(this)}>Publish</Button>}
          {uiConfig.shouldShowPublishCancelButton() && <Button variant="outlined" style={{ fontWeight: 'bold', backgroundColor: '#e8e9ec', color: 'black', marginRight: '8px' }} onClick={integ.closeEditor}>Close</Button>}
          {!uiConfig.shouldShowPublishCancelButton() && <a target="_blank" href="https://lekh.app/help.html" className="help-link">Help</a>}
        </div>
        <div className="top-left-menu">
          <AppMenu showDialog={this.showDialog}/>
        </div>
        <div className="top-center-info">
          {userInfo.isLoggedIn() && !docInfo.data.public && <span>{docName}</span>}
          {userInfo.isLoggedIn() && docInfo.data.public && <span>{moveLink} this trial board into your account</span>}
          {!loading && !userInfo.isLoggedIn() && !docInfo.data.public && <span>{docName}</span>}
          {!loading && !userInfo.isLoggedIn() && docInfo.data.public && <span>{loginLink} to save this trial board</span>}
        </div>
        {!readOnly && <div className="side-bar">
           <SideBarMenu />
        </div>}
        <Footer/>
        {menuState.cameraActive && <GesturePanel></GesturePanel>}
        {layover}
        {this.state.dialog}
        {this.state.ctxMenu && <ContextMenu showHandler={this.showContextMenu} pos={this.state.ctxMenuPos} dialogHandler={this.showDialog}/>}
        <DragPanel></DragPanel>
        <ShapeProps></ShapeProps>
        {!loading && <QuickTip></QuickTip>}
      </div>
    );
  }

  componentDidMount() {
    initializer.init(this.wsCallback);
    AppInput.showContextMenu = this.showContextMenu;
    if (uiConfig.isIntegMode()) {
      integ.setShowDialogHandler(this.showDialog, this.wsCallback);
    }
  }
}

export default AppComponent;
