import initializer from '../../common/AppInit'

function reload() {
    window.location.reload();
}

function continueTrial() {
    initializer.createNewDoc();
}

const Layover = (props) => {
    // init, disconnected, notfound, prompt, forbidden, newwait
    var item = null;
    if (props.wsstate == "init" || props.wsstate == "ready") {
        item = <div className="layover-box">Loading..</div>;
    } else if (props.wsstate == "disconnected") {
        item = <div className="layover-box">
            <p><span>Disconnected from server </span><a href="#" onClick={reload}>Reconnect</a></p>
            </div>;
    } else if (props.wsstate == "notfound") {
        item = <div className="layover-box">
            <span>Requested board does not exist</span>
            </div>;
    } else if (props.wsstate == "forbidden") {
        item = <div className="layover-box">
                <span>You do not have permission to access this board</span><br/>
                <span><a href="javascript:void(0)" onClick={reload}>Reload</a> the page to check access again</span>
            </div>;
    } else if (props.wsstate == "prompt") {
        item = <div className="layover-box">
            <a href="/?_new=true">Start a collaborative board</a>
            <a href="/?offline=true">Start an offline board</a>
            </div>;
    } else if (props.wsstate == 'newwait') {
        item = <div className="layover-box">
            <span>You are opening a trial board without login.<br/>This board will be deleted in a few days.<br/><br/>Please login to save your work</span>
            <a href="/#/login">Login</a>
            <a href="javascript:void(0)" onClick={continueTrial}>Continue trial</a>
            </div>;
    } else if (props.wsstate == 'saving') {
        item = <div className="layover-box">Saving..</div>;
    } else if  (props.wsstate == 'closing') {
        item = <div className="layover-box">Closing..</div>;
    } else if (props.wsstate == 'importing') {
        item = <div className="layover-box">Importing..</div>;
    } else if (props.wsstate == 'exporting') {
        item = <div className="layover-box">Exporting..</div>;
    } else {
        item = <div className="layover-box">
            <span>Something went wrong</span>
            <a href="#" onClick={reload}>Reload</a>
            <a href="/?offline=true">Start an offline board</a>
            </div>;
    }

    return (
        <div className="layover-col">
            {item}
        </div>
    )
}

export default Layover
